<template>
	<view class="imglistbx">
		// 单张图片
		<u-upload
			ref="uUpload"
			:auto-upload="false"
			:file-list="fileList"
			max-count="1"
			accept="image/jpeg,image/png"
			:action="action"
			:max-size="2 * 1024 * 1024"
			@oversize="oversize"
			:sizeType="sizeType"
			:show-progress="false"
			width="150"
			height="150"
			@on-choose-complete="afterRead"
		></u-upload>
<!-- 
1、选择图片
2、图片转base64
3、获取base64
 -->
	</view>
</template>

<script>
export default {
	data() {
		return {
			// 参数
			fileList: [], // 图片文件
			multipleImgs: [], // 多张图片
			action: 'https://aip.baidubce.com/rest/2.0/face/v3/detect?access_token=24.196cf95d681e87597ba65d077ab0826c.2592000.1610203725.282335-23132800',
			sizeType: ['compressed']
		};
	},
	methods: {
		// 文件超出大小限制
		oversize() {
			uni.showToast({
				title: '图片最大不能超过2M',
				icon: 'none'
			});
		},
		// 读取文件 -- 单个
		afterRead() {
			this.multipleUpload(this.$refs.uUpload.lists, 0);
		},
		// 读取文件 多张图片上传
		afterReadImgs(event) {
			this.multipleUpload(this.$refs.uUploadMultiple.lists, 2);
		},
		// 多张图片上传
		multipleUpload(event, type) {
			let that = this;
			let num = 9;
			event.map(item => {
				if (num === event.length) {
					uni.showToast({
						title: '最多上传9张图片',
						icon: 'none'
					});
					return;
				}
				num += 1;
				if (item.file) {
					that.uploadDo(item, type);
				}
			});
		},
		// 执行上传
		uploadDo(event, type) {
			let that = this;
			uni.uploadFile({
				url: 'https://aip.baidubce.com/rest/2.0/face/v3/detect?access_token=24.196cf95d681e87597ba65d077ab0826c.2592000.1610203725.282335-23132800',
				filePath: event.file.path,
				name: 'uploadFile',
				formData: {
					user: 'test'
				},
				success(res) {
					console.log(event.file.path);
					let resp = JSON.parse(res.data);
					if (resp && resp.state === 200) {
						let obj = {};
						obj.url = that.requestUrl.api.fileUrl + resp.data.path;
						obj.name = '图' + that.avatar.length + 1;
						that.fileList = [];
						that.fileList.push(obj);
						that.$refs.uUpload.clear();
					}
				}
			});
		},
		// 删除店内图片
		deleteImgs(index, lists, name) {
			this.deleteDo(index);
		},
		// 执行删除
		deleteDo(event) {
			let that = this;
			let delList = this.multipleImgs.splice(event, 1);
			let paths = delList.map(_ => _.url);
			let params = {
				pathList: paths
			};
			that.$ajax.post('删除图片接口', params).then(res => {
				if (res.statusCode === 200 && res.data.state === 200) {
					console.log('删除图片');
				} else {
					uni.showToast({
						title: res.data.message,
						icon: 'none'
					});
				}
			});
		}
	}
};
</script>

<style scoped>
.imglistbx {
	width: 100%;
	height: 100%;
}

.imglistItem {
	position: relative;
	float: left;
	margin-bottom: 20rpx;
	border-radius: 10rpx;
}

.column3 {
	width: 33.3333%;
	height: 160rpx;
}

.column4 {
	width: 25%;
	height: 130rpx;
}

.itemImg {
	width: 70%;
	height: 100%;
	margin: 0 auto;
	display: block;
	border-radius: 10rpx;
}

.cancelBtn {
	position: absolute;
	top: -10rpx;
	right: 10rpx;
}
.uploadControl {
	font-size: 50rpx;
	color: #888;
	background-color: #eeeeee;
	display: flex;
	justify-content: center;
	align-items: center;
}
.clear {
	clear: both;
}
</style>
